/* POPULAR COURSES */
.courses {
    padding-top: 4rem;
    background-color: #1f2641;
    padding-bottom: 6rem;
}

.course_image img {
    width: 100%;
    object-fit: contain;
    display: block;
}

.courses_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}

.course {
    background-color: var(--color-bg1);
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.course:hover {
    background: #1f2641;
    border-color: var(--color-primary);
}

.course_info {
    padding: 2rem;
}

.course_info p {
    margin: 1.2rem 0 2rem;
    font-size: 0.9rem;
}

/* RESPONSIVE DESIGN */
@media screen and (max-width: 1024px) {
    /* POPULAR COURSES */
    .courses {
        margin-top: 0;
    }
    
    .courses_container {
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 600px) {
    /* POPULAR COURSES */
    .courses_container {
        grid-template-columns: 1fr;
    }
}