.info-container {
    width: 100%;
    height: 300px;
    background-color: #fff;
    color: #000;
    margin-bottom: 100vh;
}

.info {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

@media screen and (max-width:960px){
    .info-container {
        height: 200px;
        width: 100%;
    }
    
    .info > h1 {
        text-align: center;
        font-size: 25px;
    }
}