.carousel-container {
    display: flex;
    vertical-align: center;
    align-items: center;
    background-color: #424890;
    padding: 30px 0 30px 0;
}

.carousel-title {
    color: #fff;
    width: 30%;
    text-align: center;
    font-size: 30px;
    padding: 0 100px 0 100px;
}

.slide-container {
    height: 80%;
    border-radius: 40px;
    overflow: hidden;
    position: relative;
    cursor: pointer;
}

.slide-container > img {
    object-fit: contain;
    transition: all 0.2s ease-in-out;
}

.slide-container:hover > img {
    transform: scale(1.2);
    opacity: 0.7;
}

.slide-desc {
    background-color: #000;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 70%;
    padding-left: 30px;
    border-top-left-radius: 20px;
    opacity: 0.8;
}

.slide-desc > span {
    color: #fff;
    display: block;
    font-size: 22px;
    padding: 0 20px 0 10px;   
}

.BrainhubCarousel__arrows {
    border-radius: 50%;
    margin: 20px;
}

